import React, { useState, useEffect } from 'react';
import * as pdfjsLib from 'pdfjs-dist/legacy/build/pdf';
import '../css/DisplayPdf.css'

pdfjsLib.GlobalWorkerOptions.workerSrc = `/pdf.worker.js`;



const DisplayPdf = ({ file }) => {
    const [pdfContent, setPdfContent] = useState([]);
    const [analysisResults, setAnalysisResults] = useState(null);
  
    const readPdf = async (file) => {
        const fileReader = new FileReader();
        fileReader.onload = async (event) => {
          const typedArray = new Uint8Array(event.target.result);
          const pdf = await pdfjsLib.getDocument({ data: typedArray }).promise;
      
          const content = [];
          for (let i = 1; i <= pdf.numPages; i++) {
            const page = await pdf.getPage(i);
            const textContent = await page.getTextContent();
            let lastY, textItems = [];
            textContent.items.forEach((item, index) => {
              if (lastY !== undefined && Math.abs(lastY - item.transform[5]) > 10) {
                textItems.push(<br key={'br-' + i + '-' + index}/>);
              }
              lastY = item.transform[5];
              const style = {
                transform: `translate(${item.transform[4]}px, ${item.transform[5]}px)`,
                fontSize: '16px'
                
              };
              textItems.push(<span style={style} key={i + '-' + item.str}>{item.str}</span>);
            });
            content.push(<div key={'page-' + i}>{textItems}</div>);
          }
          setPdfContent(content);
        };
        fileReader.readAsArrayBuffer(file);
      };
  
    useEffect(() => {
      if (file) {
        readPdf(file);
      }
    }, [file]);


    const handleAnalyzeText = async () => {
        try {
          const response = await fetch('http://localhost:5000/process-text', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({ text: pdfContent }),
          });
          const analysisResult = await response.json();
          setAnalysisResults(analysisResult);
          console.log(analysisResult);
          
        } catch (error) {
          console.error('Error:', error);
        }
      };


  
    return (
      <div className='pdf-text-container'>
        {pdfContent}
        <button onClick={handleAnalyzeText}>Analyze Text</button>
        
        {analysisResults && 
            <div className="analysis-results">
                {analysisResults.split('\n').map((line, index) => (
                <div key={index}>{line}</div>
                ))}
            </div>
        }
      </div>
    );
  };

export default DisplayPdf;